<template>
  <section class="navigation">
    <button
      @click="navigatePrevious"
      class="previous-btn"
      :disabled="this.currentStep == 1"
    >
      Previous
    </button>
    <button
      type="submit"
      class="next-btn"
      @click="navigateNext"
      v-if="this.currentStep !== this.totalSteps"
      :disabled="!valid"
    >
      Next
    </button>
    <button
      type="submit"
      class="finsih-btn"
      v-if="this.currentStep == this.totalSteps"
      :disabled="!valid"
      @click="finishForm"
    >
      Finish
    </button>
  </section>
</template>

<script>
export default {
  props: {
    valid: {
      type: Boolean,
    },
  },
  computed: {
    currentStep: {
      get() {
        return this.$store.state.currentStep;
      },
      set(value) {},
    },
    stepLength: {
      get() {
        var stpLng = (1 / Object.keys(this.$store.state.steps).length) * 100;
        return stpLng;
      },
    },
    totalSteps: {
      get() {
        var total = Object.keys(this.$store.state.steps).length;
        return total;
      },
    },
  },
  methods: {
    navigateNext() {
      if (this.currentStep <= this.totalSteps - 1) {
        this.$store.commit("updateCurrentStep", {
          currentStep: this.currentStep + 1,
        });
        this.setProgress();
        this.$router.push(`/add/${this.currentStep}`);
      }
    },
    navigatePrevious() {
      if (this.currentStep !== 0) {
        this.$store.commit("updateCurrentStep", {
          currentStep: this.currentStep - 1,
        });
        this.setProgress();
        this.$router.push(`/add/${this.currentStep}`);
      }
    },
    setProgress() {
      this.$store.commit("setProgress", {
        progressNow: this.stepLength * (this.currentStep - 1),
      });
    },
    finishForm() {
      this.$store.commit("setProgress", {
        progressNow: "100"
      });
    }
  },
  created() {
    console.log(this.currentStep);
    console.log(this.totalSteps);
  },
};
</script>

<style scoped lang="scss"></style>
