<template>
  <section class="add">
    <div class="progress-container">
      <div class="progress" :style="{ width: progressNow }"></div>
    </div>
    <div class="add-form">
      <div class="container">
        <div class="add-form-heading">
          <h1>
            Add Place
          </h1>
        </div>
        <FormulateForm name="addNewPlaceForm">
          <transition name="fade" mode="out-in">
            <router-view />
          </transition>
        </FormulateForm>
      </div>
    </div>
  </section>
</template>

<script>
import Navigation from "./Navigation.vue";
export default {
  components: { Navigation },
  data() {
    return {};
  },
  computed: {
    progressNow: {
      get() {
        return this.$store.state.progressNow;
      },
    },
  },
  methods: {
    navigateNext() {
      this.$router.push("/add/2");
    },
    navigatePrevious() {
      this.$router.push("/add/1");
    },
  },
};
</script>

